import React from 'react';
class Login extends React.Component{

  render(){
    return (
      <div className="Login">
      
          <hr/>
          <h2 > Login to Access our service </h2>
          <hr/>
          <form>
          <table>
              <tr>
                     <td>User Name :</td>
                     <td><input type="text"></input></td>
                     
              </tr>
              <tr>
                     <td>Password :</td>
                     <td><input type="password"></input></td>
              </tr>
              <tr>
                     <td></td>
                     <td><button>Login</button></td>
              </tr>
          </table>
          </form>
      
      </div>
    );
  }
}


  
  export default Login;